<template>
  <div class="content">
      <div class="header text-center">
        <h1>DIRECTORY</h1>
      </div>

      <div class="row pt-5">
          <div class="col-md-6 pb-3 search">
              <ejs-autocomplete v-model="searchValue" @change="show()" :dataSource='items' :fields='dataFields' placeholder="Search by department name">
              </ejs-autocomplete>
          </div>
          <div class="col-md-6 pb-3">
              <select class="form-control" id="exampleFormControlSelect1" v-model="selectValue">
                <option value="" selected>All Department</option>
                <option value="Department One">Department One</option>
                <option value="Department Two">Department Two</option>
                <option value="Department Three">Department Three</option>
              </select>
          </div>

          <div class="col-12 ">
              <div class="row px-2 px-xs-4">

                  <div class="col-lg-4 col-md-6 p-2" v-for="item in filterItems" :key="item.title" >
                        <div class="card">
                            <router-link :to="`/DeptDetails/${item.id}?title=${item.title}`">
                                <div class="card-img">
                                    <img src="../assets/cvs3.png" class="card-img-top" alt="...">
                                </div>
                                <div class="card-body">
                                <h5 class="card-title">{{item.title}}</h5>
                                <p class="card-text">{{item.description}}</p>
                                </div>
                                <div class="card-footer">
                                    <p class="text-muted">
                                        <i class="fas fa-map-marker-alt"></i>
                                        <span class="ml-2">{{item.level}}</span>
                                    </p>
                                </div>
                            </router-link>
                        </div>
                  </div>
                  
              </div>
          </div>
      </div>

  </div>
</template>

<script>

export default {
  name: 'ContentComponent',
  data() {
      return{
        selectValue: '',
        searchValue:null,
        items: [
            { id:1, title:'Department One', description:'This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.', level:'Level 1'},
            { id:2, title:'Department Two', description:'This card has supporting text below as a natural lead-in to additional content.', level:'Level 2'},
            { id:3, title:'Department Three', description:'This is a wider card with supporting text below as a natural lead-in to additional content. This card has even longer content than the first to show that equal height action.', level:'Level 3'}
        ],
        dataFields:{value: 'title'}
      }
  },
  computed: {
    filterItems (){
      if(this.selectValue || this.searchValue){
      return this.items.filter((item)=>{
        return item.title.match(this.selectValue || this.searchValue);
      })
      }else{
        return this.items;
      }
    }
  },
  methods:{
      show(){
          console.log(this.searchValue);
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../Global.scss';
@import url(http://cdn.syncfusion.com/ej2/bootstrap4.css);
.header h1{
    z-index: 99;
    margin-top: 10px;
    color: $secondary-color;
}

.card {

    cursor: pointer;
    box-shadow: 0px 0px 5px #eee;

    .card-img{
        height: 256px;
        display: flex;
        img {
            object-fit: contain;
        }
    }

    .card-body {
        height: 200px;
        overflow: hidden;
    }

    p {
        margin-top: 0;
        margin-bottom: 0;
        color: $primary-color;
    }
}

a {
    color: inherit;
    text-decoration: none;
    all: inherit;
    margin: 0px 0px;
}

@media (min-width: 992px) { 
    .header h1::after{
        content: "";
        width: 37%;
        height: 1px;
        background: #d7d7d7;
        position: absolute;
        z-index: -99;
        left: 0;
        top: 38px;
        float: left;
    }

    .header h1::before{
        content: "";
        width: 38%;
        height: 1px;
        background: #d7d7d7;
        position: absolute;
        z-index: -99;
        right: 0;
        top: 38px;
        float: left;
    }
 }

 @media (max-width: 992px) { 
    .header h1::after{
        content: "";
        width: 100%;
        height: 1px;
        background: #d7d7d7;
        position: absolute;
        z-index: -99;
        left: 0;
        top: 60px;
        float: left;
    }
    .header h1{
        margin-top: 0px;
        color: $secondary-color;
    }
 }

.e-input-group input.e-input, .e-input-group.e-control-wrapper input.e-input, .e-input-group textarea.e-input, .e-input-group.e-control-wrapper textarea.e-input {
    border-color: #ced4da;
    height: 36px;
}
</style>
